.kv {
  $self: &;
  position: relative;
  height: 100vh;
  overflow: hidden;
  &__vi-wrp {
    position: relative;
    width: 100vw;
    height: 100%;
    overflow: hidden;
  }
  &__vi-box {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    overflow: hidden;
    // transform: translate(-50%, -50%);
    &--1 {
      @include ie-only {
        z-index: -1;
      }
      transform: translate(-50%, -50%);
      background-color: #fff;
    }
    &--load {
      z-index: 10;
    }
  }
  &__vi-img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include ie-only {
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
    }
    &:not(#{$self}__vi-img--6) {
      transform: translate(-50%, -50%);
    }
  }
  &__vi-con-box {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 50%;
    width: 100%;
    height: 100%;
    transform: translate(-50%, 0);
  }
  &__vi-logo-wrp {
    position: absolute;
    top: calc(50% + 7rem);
    left: 50%;
    width: 100%;
    transform: translate(-50%, -50%);
    // .motion-txt {
    //   @include text-effect('.txt-target');
    //   .txt-target {
    //     z-index: -1;
    //     backface-visibility: visible !important;
    //   }
    //   &.txt-hello {
    //     .txt-target {
    //       z-index: 0;
    //     }
    //   }
    //   #{$self}__vi-logo-box {
    //     &--1 {
    //       .txt-target {
    //         transition-delay: 0s;
    //       }
    //     }
    //     &--2 {
    //       .txt-target {
    //         transition-delay: 0.2s;
    //       }
    //     }
    //     &--3 {
    //       .txt-target {
    //         transition-delay: 0.4s;
    //       }
    //     }
    //   }
    // }
    @include full {
      top: calc(50% + 7px);
    }
    @include mobile {
      top: calc(50% + 2rem);
      width: auto;
    }
  }
  &__vi-logo-box {
    text-align: center;
    overflow: hidden;
    &--1 {
      padding: 0 0 23rem;
      @include full {
        padding: 0 0 23px;
      }
      @include mobile {
        padding: 0 0 11rem;
      }
    }
    &--3 {
      padding: 35rem 0 0;
      @include full {
        padding: 35px 0 0;
      }
      @include mobile {
        padding: 16rem 0 0;
      }
    }
  }
  &__vi-logo-img {
    transform-style: preserve-3d;
    width: 399rem;
    @include mobile {
      width: 100%;
      max-width: 371rem;
    }
  }
  &__vi-txt-wrp {
    .motion-txt {
      // @include text-effect('.txt-target');
      @include text-effect-type2('.txt-target'); 
      .only-mo {
        display: none;
      }
      @include mobile {
        .only-pc {
          display: none;
        }
        .only-mo {
          display: inline-block;
        }
      }
    }
  }
  &__vi-txt-box {
    position: absolute;
    top: calc(50% - 16rem);
    left: calc(50% + 7rem);
    width: 100%;
    text-align: center;
    transform: translate(-50%, -50%);
    @include full {
      top: calc(50% - 16px);
      left: calc(50% + 7px);
    }
    @include mobile {
      top: calc(50% + 1rem);
      left: 50%;
    }
  }
  &__vi-txt {
    display: block;
    font-family: "Noto Sans KR Medium", sans-serif;
    font-size: 40rem;
    color: #fff;
    line-height: 60rem;
    letter-spacing: -0.3rem;
    @include full {
      font-size: 40px;
      line-height: 60px;
      letter-spacing: -0.3px;
    }
    @include mobile {
      font-size: 20rem;
      line-height: 30rem;
    }
  }
  &__txt-wrp {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
    width: 100%;
    height: calc(100% - 31rem);
    overflow: hidden;
    @include full {
      height: calc(100% - 31px);
    }
    @include mobile {
      height: 100%;
    }
    .motion-txt {
      @include text-effect('.txt-target');
      &.txt-hello {
        #{$self}__txt-inr {
          &--1 {
            .txt-target {
              transition-delay: 0.2s;
            }
          }
          &--2 {
            .txt-target {
              transition-delay: 0.8s;
            }
          }
          &--3 {
            .txt-target {
              transition-delay: 0.3s;
            }
          }
          &--4 {
            .txt-target {
              transition-delay: 0.9s;
            }
          }
          &--5 {
            .txt-target {
              transition-delay: 0.4s;
            }
          }
        }
      }
    }
  }
  &__txt-otr {
    position: relative;
    height: 100%;
  }
  &__txt-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    bottom: 5rem;
    left: 0;
    width: 100%;
    padding: 0 179rem 0 163rem;
    @include full {
      bottom: 5px;
      padding: 0 179px 0 163px;
    }
    @include mobile {
      flex-direction: column;
      // bottom: calc(50% - 60rem);
      bottom: 50%;
      padding: 0;
      height: 73%;
      max-height: 489rem;
      transform: translate(0, 50%);
    }
  }
  &__txt-inr {
    &:not(#{$self}__txt-inr--3) {
      overflow: hidden;
    }
    &--2 {
      position: relative;
      top: 3rem;
      left: 73rem;
      @include full {
        top: 3px;
        left: 73px;
      }
      @include mobile {
        top: -47rem;
        left: 0;
      }
    }
    &--4 {
      position: relative;
      top: 3rem;
      left: -58rem;
      @include full {
        top: 3px;
        left: -58px;
      }
      @include mobile {
        top: 44rem;
        left: 1rem;
      }
    }
    &--3 {
      @include mobile {
        // position: relative;
        // top: 4rem;
        position: absolute;
        top: calc(50% + 6rem);
        width: 100%;
        transform: translate(0, -50%);
      }
    }
    @include mobile {
      overflow: hidden;
    }
  }
  &__txt {
    font-family: "ambit-semi", sans-serif;
    color: #fff;
    &--lg {
      font-size: 90rem;
      @include full {
        font-size: 90px;
      }
      @include pc {
        font-size: 80rem;
      }
      @include mobile {
        font-size: 68rem;
      }
    }
    &--sm {
      font-family: "roslindaletext", sans-serif;
      font-size: 24rem;
      @include full {
        font-size: 24px;
      }
      @include mobile {
        font-size: 18rem;
      }
    }
    &--1 {
      @include ie-only {
        position: relative;
        top: 14rem;
        @include full {
          top: 14px;
        }
      }
    }
    &--5 {
      @include ie-only {
        position: relative;
        top: 14rem;
        @include full {
          top: 14px;
        }
      }
    }
  }
  &__roll-txt-box {
    width: 560rem;
    height: 90rem;
    @include full {
      width: 560px;
      height: 90px;
    }
    @include mobile {
      width: 100%;
      height: 70rem;
      &--active {
        height: 143rem;
      }
    }
  }
  &__roll-txt {
    display: block;
    position: absolute;
    top: 0;
    left: 2rem;
    width: 100%;
    font-family: "roslindaletext", sans-serif;
    font-size: 86rem;
    text-align: center;
    letter-spacing: -2.7rem;
    @include full {
      left: 2px;
      font-size: 86px;
      letter-spacing: -2.7px;
    }
    @include pc {
      font-size: 76rem;
    }
    @include mobile {
      left: 0;
      font-size: 68rem;
      letter-spacing: -1.8rem;
    }
    @include tablet {
      padding: 0 0 11rem;
    }
    &--1 {
      top: 4rem;
      @include full {
        top: 4px;
      }
      @include mobile {
        top: -1rem;
      }
      @include tablet {
        top: 0;
      }
    }
    &--3 {
      @include mobile {
        top: -1rem;
      }
      @include tablet {
        top: -5rem;
      }
    }
    &--5 {
      top: 4rem;
      left: 4rem;
      letter-spacing: -2.1rem;
      @include full {
        top: 4px;
        left: 4px;
        letter-spacing: -2.1px;
      }
      @at-root .br-win-ch & {
        top: -5rem;
        @include full {
          top: -5px;
        }
      }
      @include mobile {
        left: -2rem;
        line-height: 0.9;
      }
      @include tablet {
        top: -2rem;
      }
    }
  }
}

.img-box {
  @include img-effect;
}

.itr {
  $self: &;
  &__wrp {
    position: relative;
    padding: 278rem 0 0;
    @include full {
      padding: 278px 0 0;
    }
    @include mobile {
      padding: 143rem 0 0;
    }
    .motion-txt {
      @include text-effect('.txt-target');
    }
  }
  &__cf-box {
    margin: 0 auto;
    width: 770rem;
    @include full {
      width: 770px;
    }
    @include mobile {
      width: calc(100vw - 86rem);
    }
  }
  &__txt-inr {
    opacity: 0;
    transition: opacity 0.7s ease;
    &--active {
      opacity: 1;
    }
    &--1 {
      margin: 0 0 91rem;
      @include full {
        margin: 0 0 91px;
      }
      @include mobile {
        margin: 0 0 53rem;
      }
    }
    &--2 {
      #{$self}__txt {
        @include mobile {
          font-size: 13rem;
          letter-spacing: -0.6rem;
        }
      }
    }
  }
  &__txt-box {
    padding: 119rem 0 163rem 0;
    text-align: center;
    @include full {
      padding: 119px 0 163px 0;
    }
    @include mobile {
      padding: 49rem 0 130rem 0;
    }
  }
  &__txt {
    font-family: "Noto Sans KR Medium", sans-serif;
    font-size: 28rem;
    letter-spacing: -0.4rem;
    line-height: 1.6;
    @include full {
      font-size: 28px;
      letter-spacing: -0.4px;
    }
    @include mobile {
      font-size: 16rem;
      letter-spacing: -0.3rem;
      line-height: 1.5;
    }
  }
  &__slogan {
    margin: 0 auto 51rem;
    width: 472rem;
    @include full {
      margin: 0 auto 51px;
      width: 472px;
    }
    @include mobile {
      margin: 0 auto 26rem;
      width: 236rem;
    }
  }
  &__date {
    margin: 0 0 29rem;
    font-family: "ambit-semi", sans-serif;
    font-size: 38rem;
    letter-spacing: -0.1rem;
    @include full {
      margin: 0 0 29px;
      font-size: 38px;
      letter-spacing: -0.1px;
    }
    @include mobile {
      margin: 0 0 11rem;
      font-size: 22rem;
      letter-spacing: -0.2rem;
    }
  }
  &__info {
    margin: 0 0 7rem;
    font-family: "ambit-semi", sans-serif;
    font-size: 38rem;
    letter-spacing: -0.3rem;
    @include full {
      margin: 0 0 7px;
      font-size: 38px;
      letter-spacing: -0.3px;
    }
    @include mobile {
      margin: 0 0 6rem;
      font-size: 22rem;
      letter-spacing: -0.2rem;
    }
  }
}

